@font-face {
    font-family: poefont;
    src: url('./assets/Fonts/fontin.ttf');
}

html {
	background: url('./assets/MainBackground.jpg') no-repeat center center;
	background-size: cover;
	width: 100%;
	height: 100%;
	overflow: hidden;
}

html, body {
	padding: 0;
	margin: 0;
}

body {
	height: 100%;
}

#root {
	display: -ms-flexbox;
	display: flex;
	display: -webkit-flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: row;
	    flex-direction: row;
	height: 100%;
}

.entireContainer {
	display: -ms-flexbox;
	display: flex;
	display: -webkit-flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: row;
	    flex-direction: row;
	font-family: poefont;
  background-color: rgba(0, 0, 0, 0.8);
	height: 60%;
	width: 60%;
}

.itemStatsContainer {
	display: -ms-flexbox;
	display: flex;
	display: -webkit-flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	/* background-color: rgba(256, 0, 0, 0.2); */
  height: 100%;
  width: 50%;

}

.affixContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  height: 50%;
  width: 100%;
}

.craftingOptionsContainer {
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -ms-flex-direction: column;
      flex-direction: column;
  /* background-color: rgba(0, 256, 0, 0.2); */
  height: 100%;
  width: 50%;
}

img {
  max-width: 100%;
  max-height: 100%;
}

.itemHeaderContainer {
  position: relative;
  text-align: center;
  color: white;
  width: 100%;
}

.itemHeaderText {
  position: absolute;
  font-size: 2vw;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

#itemHeaderImage {
  width: 100%;
}

.itemImageDiv {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
  height: 50%;
  width: 100%;
}

.itemProperties {
  margin: 3px;
}

.tooltipText {
	text-align: center;
  font-size: 1.5vw;
}

#affix, #qualityValue, #propertyValue{
	color: #88f;
}

#propertyText {
	color: #8f8f8f;
}

.buttonContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
	/* background-color: rgba(0, 0, 256, 0.2); */
  width: 100%;
  height: 40%;
}

button {
  width: 15%;
  max-width: 46px;
  min-width: 30px;
  max-height: 46px;
  min-height: 30px;
  margin: 5px;
	border: none;
}

#transmute {
	background: url('./assets/Buttons/Transmute.png') no-repeat;
	background-size: 100%;
}

#augment {
	background: url('./assets/Buttons/Augment.png') no-repeat;
	background-size: 100%;
}

#alteration {
	background: url('./assets/Buttons/Alteration.png') no-repeat;
	background-size: 100%;
}

#regal {
	background: url('./assets/Buttons/Regal.png') no-repeat;
	background-size: 100%;
}

#exalt {
	background: url('./assets/Buttons/Exalt.png') no-repeat;
	background-size: 100%;
}

#chaos {
	background: url('./assets/Buttons/Chaos.png') no-repeat;
	background-size: 100%;
}

#alchemy {
	background: url('./assets/Buttons/Alchemy.png') no-repeat;
	background-size: 100%;
}

#scour {
	background: url('./assets/Buttons/Scour.png') no-repeat;
	background-size: 100%;
}

#annulment {
	background: url('./assets/Buttons/Annulment.png') no-repeat;
	background-size: 100%;
}



